import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { LoginSesionPage } from '../pages/login/login-sesion/login-sesion';
import { HomePage } from '../pages/home/home';
import { DatabaseConnectionProvider } from '../providers/database-connection/database-connection';
import { TabsPage } from '../pages/tabs/tabs';
import { ExplorarPage } from '../pages/explorar/explorar';
import { MensajesPage } from '../pages/mensajes/mensajes';
import { FavoritosPage } from '../pages/favoritos/favoritos';
import { ReservasPage } from '../pages/reservas/reservas';
import { PerfilPage } from '../pages/perfil/perfil';
import { RegistroProvider } from '../providers/registro/registro';
import { ExitPage } from '../pages/exit/exit';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    LoginSesionPage,
    TabsPage,
    MensajesPage,
    ExplorarPage,
    FavoritosPage,
    ReservasPage,
    PerfilPage,
    ExitPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/explorar/explorar.module#ExplorarPageModule', name: 'ExplorarPage', segment: 'explorar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/favoritos/favoritos.module#FavoritosPageModule', name: 'FavoritosPage', segment: 'favoritos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reservas/reservas.module#ReservasPageModule', name: 'ReservasPage', segment: 'reservas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login-sesion/login-sesion.module#LoginSesionPageModule', name: 'LoginSesionPage', segment: 'login-sesion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/exit/exit.module#ExitPageModule', name: 'ExitPage', segment: 'exit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module#TabsPageModule', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mensajes/mensajes.module#MensajesPageModule', name: 'MensajesPage', segment: 'mensajes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/perfil/perfil.module#PerfilPageModule', name: 'PerfilPage', segment: 'perfil', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/special/special.module#SpecialPageModule', name: 'SpecialPage', segment: 'special', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module#MenuPageModule', name: 'MenuPage', segment: 'menu', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    LoginSesionPage,
    TabsPage,
    MensajesPage,
    ExplorarPage,
    FavoritosPage,
    ReservasPage,
    PerfilPage,
    ExitPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    DatabaseConnectionProvider,
    RegistroProvider
  ]
})
export class AppModule { }



